<template>
  <div>
    <h2>申请状态</h2>
    <el-card>
      <el-table :data="state_form"  >
      <!-- <el-table-column type="index"></el-table-column> -->
      <el-table-column prop="姓名" label="姓名"></el-table-column>
      <el-table-column prop="学号" label="学号"></el-table-column>
      <el-table-column prop="申请时间" label="申请时间"></el-table-column>
      <el-table-column prop="请假类型" label="请假类型"></el-table-column>
      <el-table-column prop="审批结果" label="审批结果"></el-table-column>
      </el-table>
    </el-card>
    
  </div>
</template>

<script>
export default {
  data(){
    return {
      state_form:[]
    }
  },
  created(){
    this.getstate_form()
  },
  methods:{
    async getstate_form(){
      const {data:res}=await this.$http.get('/check',{});
      this.state_form=res.data;
      if(res.code == 200) return this.$message.success('获取数据成功!')  
    }
  }
}
</script>

