<template>
    <div class="forgetContainer">
        <div class="forget_box">
            <h2 class="head">忘记密码</h2>
            <el-form label-width="80px" :model="forgetForm" ref="forgetFormref" :rules="forgetFormrules">
            <el-form-item label="学号" prop="stu_id" >
              <el-input v-model="forgetForm.stu_id"></el-input>
              <div class="send">
                <el-link :underline="false" @click="send_code">发送验证</el-link>
              </div>
            </el-form-item>        
            <el-form-item label="验证码" prop="code" >
                <el-input v-model="forgetForm.code"></el-input>
            </el-form-item>
            <el-form-item class="btns">
              <el-button type="primary" @click="nextstep ">下一步</el-button>
            </el-form-item>
            </el-form>
            </div>
            <!-- 密码的对话框 -->
            <el-dialog title="修改密码" :visible.sync="this.mima">
              <el-form :model="form" ref="Formref" >
                <el-form-item label="密码" prop="password">
                <el-input  v-model="Form.password"></el-input>
              </el-form-item>
              </el-form>
              <span slot="footer" class="dialog-footer">
                <el-button @click="mima = false">取 消</el-button>
                <el-button type="primary" @click="login">确 定</el-button>
              </span>
            </el-dialog>           
    </div>

</template>

<script>
export default {
    data() {
      return {
        // 隐藏对话框
        mima:false,
        vrefiy:"",
        forgetForm:{
          stu_id:'',
            code:'',
        },
        Form:{
          password:''
        },
        Formref:{
          password: [
            { required:true,message:'请输入密码', trigger: 'blur' },
            // { pattern:/^ [0-9] {6,18}$/, message: '长度在6到15之间的数字',trigger:'blur'}
            { min:6,max:15,message:'密码在6-15位',trigger:'blur' }
            ]
        },
        forgetFormrules:{
          stu_id:[
          { required:true,message:'请输入学号',trigger:'blur' },
          { pattern:/^[2][0-9][0-9]{9}$/, message: '长度在11位',trigger:'blur' }
          ],
          code:[
              { required:true,message:'请输入验证码',trigger:'blur' },
              { min:4,max:4,message:'验证码有6位',trigger:'blur' }
          ],
          
        }
        };
    },
    methods: {
    async send_code(){
      const {data:res}= await this.$http
          .post("/email",{
            stu_id:this.forgetForm.stu_id
          })
          this.$message.success("发送成功")
          this.vrefiy = res.data
      },
      nextstep() {
        if(this.vrefiy==this.forgetForm.code){
          this.mima=true
          alert('验证码正确')
        }
        else  alert('验证码错误')
      },
      async login() {
        // 发送注册(post)请求
        const {data:res}=await this.$http
          .post("/forgetpwd",{
            stu_new_password:this.Form.password,
            stu_id:this.forgetForm.stu_id
          })         
            if(res.code == 200){             
              //  成功后直接跳转到登录页
            alert('修改成功！')
            this.$router.push("/login");
            }
      },
      // 点击回车键登录
      keyDown(e) {
        // 回车则执行登录方法 enter键的ASCII是13
        if (e.keyCode === 13) {
          this.login(); // 定义的登录方法
        }
      },
      // 绑定监听事件
      mounted() {
        window.addEventListener("keydown", this.keyDown);
      },
      // 销毁事件
      destroyed() {    
        window.removeEventListener("keydown", this.keyDown, false);
      },
      // open() {
      //     this.$alert('这是一段内容', '标题名称', {
      //       confirmButtonText: '确定',
      //       callback: action => {
      //         this.$message({
      //           type: 'info',
      //           message: `action: ${ action }`
      //         });
      //       }
      //     });
      //   }
      }
}

</script>

<style lang="less" scoped>
.forgetContainer{
    height: 100%;
    // background: linear-gradient(#0bd4a5, #3897e5);
    background-image: url(../assets/picture1.jpg);
  background-size: 100% 100%;
}
.forget_box{
    width: 500px;
    height: 600px;
    /* background-color: blue; */
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);

}
.head{
    display: flex;
    justify-content: center;
}
.send{
    height: 35px;
    width: 100px;
    background-color: #409EFF;
    border-radius: 30px;
    border: 4px solid #409EFF;
    display: flex;
    justify-content: center;
    float: right;

}
.forget_box
.send:visited{
  color: #fff;
}
.btns{
  display: flex;
  justify-content: flex-end;
}
</style>