<template>
  <div class="enroll_container">
    <div class="enroll_box">
      <h3>注册账号</h3>
      <el-form ref="enrollformref" label-width="70px" class="enroll_information" :model="enroll_form" :rules="enroll_rules">
       <el-form-item label="学号" prop="stu_id">
       <el-input v-model="enroll_form.stu_id"></el-input>
       </el-form-item>
       <el-form-item label="姓名" prop="stu_name">
       <el-input v-model="enroll_form.stu_name"></el-input>
       </el-form-item>
       <el-form-item label="电话" prop="phone_num">
       <el-input v-model="enroll_form.phone_num"></el-input>
       </el-form-item>
       <el-form-item label="班级" prop="stu_class" >
       <el-input v-model="enroll_form.stu_class" placeholder="计算机类21202班"></el-input>
       </el-form-item>
       <el-form-item label="辅导员" prop="teacher">
       <el-input v-model="enroll_form.teacher"></el-input>
       </el-form-item>
       <el-form-item label="邮箱" prop="email">
       <el-input v-model="enroll_form.email" class="enroll_email"></el-input>
       <el-button type="primary" class="fetch" @click="email_fetch">获取验证码</el-button>
       </el-form-item>
      
       <el-form-item label="验证码" prop="code">
       <el-input v-model="enroll_form.code"></el-input>
       </el-form-item>
       <el-form-item class="btns">
        <el-button type="primary" @click="nextstep">输入密码</el-button>
        <el-button type="primary" @click="reset_erollform">重置</el-button>
       </el-form-item>
      </el-form> 
    </div>

    <!-- 密码的对话框 -->
    <el-dialog title="修改密码" :visible.sync="this.mima">
        <el-form :model="form" ref="Formref">
          <el-form-item label="密码" prop="password">
          <el-input  v-model="form.password"></el-input>
        </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="mima = false">取 消</el-button>
          <el-button type="primary" @click="submit">确 定</el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>

export default {
  data(){
    return {
      // 隐藏对话框
      mima:false,
      vrefiy:"",
      enroll_form:{
        stu_id:'',
        stu_name:'',
        phone_num:'',
        stu_class:'',
        teacher:'',
        email:'',
        code:'',
      },
      form:{
        password:''
      },
      Formref:{
          password: [
            { required:true,message:'请输入密码', trigger: 'blur' },
            // { pattern:/^ [0-9] {6,18}$/, message: '长度在6到15之间的数字',trigger:'blur'}
            { min:6,max:15,message:'密码在6-15位',trigger:'blur' }
            ]
        },
      enroll_rules:{
        stu_name:[
        { required: true, message:'请输入名字', trigger: 'blur' },
        { pattern:/^[\u4E00-\u9FA5]{2,10}(·[\u4E00-\u9FA5]{2,10}){0,2}$/, message: '给我好好输入你的名字', trigger: 'blur' }
        ],
        stu_id:[
        { required: true, message:'请输入账号', trigger: 'blur' },
        { pattern:/^[2][0-9][0-9]{9}$/, message: '请正确输入', trigger: 'blur' }
        ],
        phone_num:[
        { required: true, message:'请输入电话号码', trigger: 'blur' },
        { pattern:/^[1][3,4,5,7,8][0-9]{9}$/, message: '请正确输入', trigger: 'blur' }
        ],
        stu_class:[
        { required: true, message:'请输入班级', trigger: 'blur' },
        { min: 5, max: 13, message: '请输入班级', trigger: 'blur' }
        ],
        teacher:[
        { required: true, message:'请输入辅导员的名字', trigger: 'blur' },
        { pattern:/^[\u4E00-\u9FA5]{2,10}(·[\u4E00-\u9FA5]{2,10}){0,2}$/, message: '请重新输入辅导员的名字', trigger: 'blur' }
        ],
        email:[
        {required: true, message: '请输入邮箱', trigger: 'blur'},
        {pattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/, message:"邮箱的格式不正确!", trigger: 'blur'}
        ],
        code:[
        { required: true, message:'请输入验证码', trigger: 'blur' },
        { min: 4, max: 4, message: '请输入验证码', trigger: 'blur' }
        ]
      }
    }
  },
  methods:{
    reset_erollform(){
      this.$refs.enrollformref.resetFields();
    },
    async email_fetch(){
      const {data:res} = await this.$http.post("/hsqsenddemail",{email:this.enroll_form.email})
      this.$message.success("发送成功")  
      this.vrefiy = res.msg  
    },
    nextstep() {
          if(this.vrefiy==this.enroll_form.code){
            this.mima=true
            alert('验证码正确')
          }
          else  alert('验证码错误')
    },
    async submit() {
        // 发送注册(post)请求
       const{data:res}=await this.$http.post("/enroll",{
        stu_id:this.enroll_form.stu_id,
        stu_class:this.enroll_form.stu_class,
        password:this.form.password,
        stu_name:this.enroll_form.stu_name,
        phone_num:this.enroll_form.phone_num,
        teacher:this.enroll_form.teacher,
        email:this.enroll_form.email,
        code:this.enroll_form.code
       })
            if (res.code == 200) {
              alert('注册成功')
              // 注册成功后直接跳转到登录页
              this.$router.push("/login");
            } 
     },
    // 点击回车键登录
    keyDown(e) {
      // 回车则执行登录方法 enter键的ASCII是13
      if (e.keyCode === 13) {
        this.login(); // 定义的登录方法
      }
    },
     // 绑定监听事件
    mounted() {
      window.addEventListener("keydown", this.keyDown);
    },
     // 销毁事件
    destroyed() {    
      window.removeEventListener("keydown", this.keyDown, false);
    },
  }
}
</script>

<style lang="less" scoped>
.enroll_container{
  // background: linear-gradient(#0bd4a5, #3897e5);
  background-image: url(../assets/picture5.jpg);
  background-size: 100% 100%;
  height: 100%;
}
.enroll_box{
  width: 350px;
  height: 660px;
  // background: linear-gradient(#0bd4a5, #3897e5);
  background: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}
h3{
  display: flex;
  justify-content: center;
}
.btns{
  display: flex;
  justify-content: flex-end;
}
.enroll_information{
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 10px;
  box-sizing: border-box;
}
.enroll_box
.enroll_email{
  width: 150px;
 border: 3px solid rgb(240, 237, 237); 
} 
.fetch{
  width: 100px;
  display: flex;
}
</style>