<template>
  <div class="LoginContainer">
    <div class="login_box" >
      <!-- 头像区域 -->
    <div class="avatar_box">
      <img src="../assets/logo.jpg" alt="">
    </div>
    <!-- 登录表单区 -->
    <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form" >
      <!-- 用户名 -->
      <el-form-item prop="stu_id">
        <el-input v-model="loginForm.stu_id" prefix-icon="iconfont icon-geren"  placeholder="账号"></el-input>
      </el-form-item>
      <!-- 密码 -->
      <el-form-item prop="password"> 
        <el-input v-model="loginForm.password" prefix-icon="iconfont icon-mima"  placeholder="密码" type="password"></el-input>
      </el-form-item>
      <el-link @click="forget">忘记密码</el-link>
      <!-- 按钮 -->
      <el-form-item class="btns" @keyup.Enter="login">
        <el-button type="primary" @click="login" @keyup.enter="login">登录</el-button>
        <el-button type="primary" @click="resetLoginForm">注册</el-button>
      </el-form-item>
    </el-form>
    </div>

  </div>
</template>

<script>
// import { getRequest} from "./plugins/LoginApi.js"
export default {
  data(){
    return {
      // 这是登录表单的数据绑定对象 
      loginForm: {
        stu_id:'',
        password:''
      },
      list:[],
      // 这是表单验证规则对象
      loginFormRules: {
        // 验证用户名是否合法
        stu_id:[
        { required: true, message:'请输入账号', trigger: 'blur' },
        // { pattern:/^[2][0-9][0-9]{9}$/, message: '请正确输入', trigger: 'blur' },
        ],
        // 验证密码是否合法
        password:[
        { required: true, message:'请输入密码', trigger: 'blur' },
        // { pattern:/^ [0-9] {6,15}$/, message: '密码是长度在6到15之间的数字', trigger: 'blur' }
        ]
      }

    }
  },
  methods:{
    // 重置表单
    resetLoginForm(){
      this.$refs.loginFormRef.resetFields();
    },

   // 登录
    async login(){
      const {data:res} = await this.$http.post("/login",{
        stu_id:this.loginForm.stu_id,password:this.loginForm.password
      })
      console.log(res);
      // 将登录成功之后的 token，保存到客户端的 sessionStorage 中
      sessionStorage.setItem("token", res.token);
      // 登录成功后直接跳转到主页
     if(res.code == 200){
      this.$router.push("/Apply")
     }else if (res.code == 100){
      alert(res.data)
     }
    },
    // 点击回车键登录
    keyDown(e) {
      // 回车则执行登录方法 enter键的ASCII是13
      if (e.keyCode === 13) {
        this.login(); // 定义的登录方法
      }
    },
     // 绑定监听事件
    mounted() {
      window.addEventListener("keydown", this.keyDown);
    },
     // 销毁事件
    destroyed() {    
      window.removeEventListener("keydown", this.keyDown, false);
    },
    resetLoginForm(){
      this.$router.push('/Enroll')
    },
    forget(){
      this.$router.push('/Forget')
    }
  } 
}
</script>

<style lang="less" scoped>
// 背景
.LoginContainer{
  background-image: url(../assets/picture3.jpg);
  background-size: 100% 100%;
  // background: linear-gradient(#0bd4a5, #3897e5);
    min-width: calc(100vw);
    height: 100%;
}
// 登录
.login_box{
  width: 350px;
  height: 300px;
  background: #fff;
  // min-width: calc(100vw);
  border-radius: 3px;
  // 绝对定位
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);

  // 嵌套
  .avatar_box{
    height: 130px;
    width: 300px;
    border:1px solid #eee;
    //调圆角
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    background-color: #fff;
    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #eee;
    }
  }
}
.login_form{
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing:border-box;
}
.btns{
  display: flex;
  justify-content: flex-end;
}
</style>