<template>
<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" >
  <el-form-item label="开始请假时间" required >
    <el-col :span="5">
      <el-form-item prop="date1" >
        <el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.date1" style="width: 60%;" value-format="yyyy-MM-dd"></el-date-picker>
      </el-form-item>
    </el-col>    
    <el-col :span="5" >
      <el-form-item prop="date2" >
        <el-time-picker placeholder="选择时间" v-model="ruleForm.date2" style="width: 60%;" value-format="HH:mm:ss"></el-time-picker>
      </el-form-item>
    </el-col>
  </el-form-item>
  <el-form-item label="请假结束时间" required >
    <el-col :span="5">
      <el-form-item prop="date3" >
        <el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.date3" style="width: 60%;" value-format="yyyy-MM-dd"></el-date-picker>
      </el-form-item>
    </el-col>

    <el-col :span="5" >
      <el-form-item prop="date4" >
        <el-time-picker placeholder="选择时间" v-model="ruleForm.date4" style="width: 60%;" value-format="HH:mm:ss"></el-time-picker>
      </el-form-item>
    </el-col>
  </el-form-item>
  <el-form-item label="请假理由" prop="le_type">
    <el-radio-group v-model="ruleForm.le_type">
      <el-radio label="事假"></el-radio>
      <el-radio label="病假"></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="请假原因" prop="le_why">
    <el-input type="textarea" v-model="ruleForm.le_why"></el-input>
  </el-form-item>
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm')" @keyup.enter="login">提交申请</el-button>
    <el-button @click="resetForm('ruleForm')">重置</el-button>
  </el-form-item>
</el-form>
</template>

<script>
export default {
    data() {
      return {
        ruleForm: {
          date1: '',
          date2: '',
          date3: '',
          date4: '',
          le_type: '',
          le_why: ''
        },
        rules: {
          date1: [
            { type: 'date', required: true, message: '请选择请假日期', trigger: 'change' }
          ],
          date2: [
            { type: 'date', required: true, message: '请选择请假时间', trigger: 'change' }
          ],
          date3: [
            { type: 'date', required: true, message: '请选择收假日期', trigger: 'change' }
          ],
          date4: [
            { type: 'date', required: true, message: '请选择收假时间', trigger: 'change' }
          ],
          le_type: [
            { required: true, message: '请选择请假理由', trigger: 'change' }
          ],
          le_why: [
            { required: true, message: '请填写请假原因', trigger: 'blur' }
          ]
        }
      };
    },
    created(){
    },
    methods: {
      async submitForm(){
        // 拼接时间
        let time_bg=this.ruleForm.date1+this.ruleForm.date2
        let time_la=this.ruleForm.date3+this.ruleForm.date4
        const {data:res} = await this.$http.post("/cause",{
            le_type:this.ruleForm.le_type,
            le_why:this.ruleForm.le_why,
            le_time_bg:time_bg,
            le_time_end:time_la})
            if (res.code == 200) {
              alert('申请成功!')  
          }
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style lang="less" scoped>
.use_name{
  width: 323px;
}
.apply_region{
  width: 323px;
}
</style>